import React, { useEffect, useState, useCallback } from "react";
import { Col, Row, Tabs, Tab, Button } from "react-bootstrap";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import defenseIcon from "../../assets/defenseicon.png";
import strengthIcon from "../../assets/strengthicon.png";
import supportIcon from "../../assets/supporticon.png";
import magicIcon from "../../assets/magicicon.png";
import "./soul.css";
import Artifact from "./Artifact";

function Soul(props) {
	const [card, setCard] = useState({
		name: "",
		id: "",
		attr: "",
		rarity: "",
		descr: "",
		profile: {
			type: "",
			origin: "",
			age: "",
			nature: "",
			nexus: [],
			height: "",
			motto: "",
		},
		story: {
			1: { name: "", descr: "" },
			2: { name: "", descr: "" },
			3: { name: "", descr: "" },
			4: { name: "", descr: "" },
			5: { name: "", descr: "" },
		},
	});
	const [profile, setProfile] = useState(true);
	const params = useParams();
	const { state } = useLocation();
	const navigate = useNavigate();
	// eslint-disable-next-line
	useEffect(
		useCallback(() => {
			if (state?.type === "Soul_View" && state?.payload) {
				setCard((prevState) => {
					let newCard = { ...prevState };
					newCard = state.payload;
					newCard.arti = require("../../assets/cardArtiMod.json")[
						newCard.id
					];
					return newCard;
				});
			} else {
				let cardJson = require("../../assets/cardMod.json");
				setCard((prevState) => {
					let newCard = { ...prevState };
					newCard = cardJson.filter((card) => card.name === params.id)[0];
					newCard.arti = require("../../assets/cardArtiMod.json")[
						newCard.id
					];
					return newCard;
				});
			}
		}, [state.type, state.payload, params.id]),
		[]
	);
	const attrImg = () => {
		switch (card?.attr) {
			case "":
				return magicIcon;
			case "":
				return strengthIcon;
			case "":
				return supportIcon;
			case "":
				return defenseIcon;
			default:
				return "";
		}
	};

	function soulTable(label, info) {
		return (
			<Row>
				<Col sm={3} className="soul-info-label">
					<div>{label}</div>
				</Col>
				<Col>
					<div>{info}</div>
				</Col>
			</Row>
		);
	}

	const soulStoryTab = (chapter) => {
		return (
			<>
				<h3 style={{ marginTop: "20px" }}>{card.story[chapter].name}</h3>
				<div className="tab-story">
					{card.story[chapter].descr.split("\n").map((str) => (
						<p>{str}</p>
					))}
				</div>
			</>
		);
	};

	const toggleProfile = () => {
		setProfile(!profile);
	};

	const scrollToTop = () => {
		window.scrollTo({
			top: 0,
			behavior: "smooth",
		});
	};

	const onBackClick = (e) => {
		navigate(e.currentTarget.dataset.page);
	};

	return (
		<div className="soul-profile">
			<Row>
				<Col sm={4} className="back-button">
					<Button
						variant="light"
						onClick={onBackClick}
						data-page="/saga-tasty"
					>
						Back
					</Button>
					<Button variant="light" onClick={toggleProfile}>
						{profile ? "Hide" : "Show"}
					</Button>
				</Col>
			</Row>
			<h2>{card.name}</h2>
			<Row>
				{profile && (
					<Col sm={4}>
						{card.profile ? (
							<>
								<img src={attrImg()} alt="attr" />
								<div>{card.attr}</div>
								{soulTable("", card.profile.type)}
								{soulTable("", card.profile.origin)}
								{soulTable(" ", card.profile.age)}
								{soulTable("", card.profile.nature)}
								{soulTable("", card.profile.height)}
								{soulTable("", "[WIP]")}
								<div style={{ fontWeight: "700" }}></div>
								<div>{card.profile.motto}</div>
								<div style={{ fontWeight: "700" }}></div>
								<div>{card.descr}</div>
							</>
						) : (
							<div>error</div>
						)}
					</Col>
				)}
				<Col>
					<Tabs defaultActiveKey="story" className="soul-tab" justify>
						<Tab eventKey="story" title="">
							{card?.story?.["1"]?.descr ? (
								<Tabs
									defaultActiveKey="first"
									className="soul-tab"
									justify
								>
									<Tab eventKey="first" title="I.">
										{soulStoryTab("1")}
									</Tab>
									<Tab eventKey="second" title="II.">
										{soulStoryTab("2")}
									</Tab>
									<Tab eventKey="third" title="III.">
										{soulStoryTab("3")}
									</Tab>
									<Tab eventKey="fourth" title="IV.">
										{soulStoryTab("4")}
									</Tab>
									<Tab eventKey="fifth" title="V.">
										{soulStoryTab("5")}
									</Tab>
								</Tabs>
							) : (
								<div className="coming-soon">Coming Soon</div>
							)}
						</Tab>
						<Tab eventKey="voice" title=" ">
							<div className="voice-content">
								{card.voice &&
									card.voice.map((voice) => {
										return soulTable(voice.name, voice.descr);
									})}
							</div>
						</Tab>
						<Tab eventKey="artifact" title="">
							{card.arti?.node1 ? (
								<Artifact arti={card.arti} />
							) : (
								<div className="coming-soon"></div>
							)}
						</Tab>
					</Tabs>
					<Button
						onClick={scrollToTop}
						variant="light"
						style={{ marginTop: "10px" }}
					>
						Top
					</Button>
				</Col>
			</Row>
		</div>
	);
}

export default Soul;
